<template>
  <img v-bind:src="createSrc" alt="test">
</template>

<script>
  export default {
    name: "ImageElements",
    data: function () {
      return {
        currentSrc: "",
        elementsImages: {
          "1": "data:image/png;base64,",
          "2": "data:image/png;base64,",
          "3": "data:image/png;base64,",
          "4": "data:image/png;base64,",
          "5": "data:image/png;base64,",
          "6": "data:image/png;base64,"
        }
      }
    },
    props: {
      elementNumber: String
    },
    methods: {
      GenerateSrc: function (number) {
        return this.elementsImages[number.toString()]
      }
    },
    computed: {
      createSrc: function () {
        return this.GenerateSrc(this.elementNumber)
      }
    },
  }
</script>

